* {
  padding: 0;
  margin: 0;
}

/* Register & Login Style */

.formContainer {
  background: #a7bcff;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.formWrapper {
  background: white;
  padding: 16px 50px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
}

.formWrapper form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.formWrapper form input {
  padding: 15px;
  border: none;
  width: 250px;
  border-bottom: 1px solid #a7bcff;
}

::placeholder {
  color: rgb(175, 175, 175);
}
.formWrapper .logo {
  color: #5d5b8d;
  font-weight: bold;
  font-size: 30px;
}

.formWrapper .title {
  color: #5d5b8d;
  font-size: 25px;
}

.formWrapper form label {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 10px;
  color: #8da4f1;
  font-size: 12px;
}

.formWrapper form img {
  width: 32px;
}

.formWrapper form button {
  background-color: #7b96ec;
  color: white;
  padding: 10px;
  font-weight: bold;
  border: none;
  cursor: pointer;
}

.formWrapper p {
  color: #5d5b8d;
  font-size: 12px;
  margin-top: 10px;
}

/* Home Style */
.home {
  background: #a7bcff;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.home .container {
  border: 1px solid white;
  border-radius: 10px;
  width: 65%;
  /* width: 100%; */
  height: 80%;
  /* height: 100%; */
  display: flex;
  overflow: hidden;
}

/* SideBar Style */
.sideBar {
  flex: 1;
  border-right: 1px solid #3e3c61;
  background-color: #3e3c61;
}

/* NavBar Style */

.navBar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #2f2d52;
  min-height: 50px;
  color: #ddddf7;
}

.navBar .logo {
  font-weight: bold;
}

.navBar .user {
  display: flex;
  gap: 10px;
}

.navBar img {
  background-color: #ddddf7;
  height: 24px;
  width: 24px;
  border-radius: 50%;
  object-fit: cover;
}

.navBar button {
  background-color: #5d5b8d;
  color: #ddddf7;
  font-size: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  padding: 5px;
}

/* Search Style */

.search {
  border-bottom: 1px solid rgb(175, 175, 175);
}

.search .searchForm {
  width: 100%;
}
.search .searchForm input {
  background-color: transparent;
  border: none;
  color: white;
  outline: none;

  width: 91%;
  padding: 15px;
}

.userChat {
  padding: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
  color: white;
  cursor: pointer;
}

.userChat:hover {
  background-color: #2f2d52;
}
.userChat img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
}

.userChatInfo span {
  font-size: 18px;
  font-weight: 500;
}

.userChatInfo p {
  font-size: 14px;
  color: lightgray;
}

/* Chat Style */
.chat {
  flex: 2;
}

.chatInfo {
  height: 50px;
  background-color: #5d5b8d;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  color: lightgray;
}

.chatIcons {
  display: flex;
  gap: 10px;
}

.chatIcons img {
  height: 24px;
  cursor: pointer;
}

/* Chat  Messages  Style */

.messages {
  background-color: #ddddf7;
  padding: 10px;
  height: calc(100% - 160px);
  overflow: scroll;
  overflow-x: auto;
}
.messages::-webkit-scrollbar {
  width: 10px;
}

.messages::-webkit-scrollbar-track {
  background: transparent;
}

.messages::-webkit-scrollbar-thumb {
  background: #5d5b8d;
  border-radius: 10px;
}

.messages::-webkit-scrollbar-thumb:hover {
  background: #2f2d52;
}

.message {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}

.owner {
  flex-direction: row-reverse;
}

.messageInfo {
  display: flex;
  flex-direction: column;
  color: gray;
  font-weight: 300;
}
.messageInfo img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
}
.messageContent {
  max-width: 80%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.messageContent p {
  background-color: white;
  padding: 10px 20px;
  border-radius: 5px;
  position: relative;
  background-color: #f2f2f2;
  max-width: max-content;
}

.owner p {
  background-color: #8da4f1;
  color: white;
  max-width: max-content;
}

.messageContent p::after {
  content: " ";
  position: absolute;
  width: 0;
  height: 0;
  left: -10px;
  right: auto;
  top: 15px;
  bottom: auto;
  border: 10px solid;
  border-color: #f2f2f2 transparent transparent transparent;
}

.owner .messageContent p::after {
  content: " ";
  position: absolute;
  width: 0;
  height: 0;
  left: auto;
  right: -10px;
  top: 15px;
  bottom: auto;
  border: 10px solid;
  border-color: #8da4f1 transparent transparent transparent;
}

.owner .messageContent {
  align-items: flex-end;
}
.messageContent img {
  width: 50%;
  border-radius: 5px;
}

/* Chat  Input  Style */

.input {
  height: 50px;
  background-color: white;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.input input {
  width: 100%;
  border: none;
  outline: none;
  color: #2f2d52;
  font-style: 20px;
}

.input input::placeholder {
  color: lightgray;
  font-style: 20px;
}

.send {
  display: flex;
  gap: 10px;
  align-items: center;
}

.send img {
  height: 24px;
  cursor: pointer;
}

.send button {
  border: none;
  padding: 10px 15px;
  color: white;
  background-color: #8da4f1;
  border-radius: 5px;
}

/* Form Image Conatiner Style */
.imageWrapper {
  display: flex;
  align-items: center;
  gap: 10px;
}

#trashIcon {
  color: #cb0606;
}

#fileName {
  font-weight: 500;
  color: #7b96ec !important;
}

#userNotFound {
  color: rgba(255, 34, 0, 0.812);
  padding-left: 25px;
}

#deleteIcon {
  width: 40px;
  height: 40px;
}

/* Nav Bar PRofile Icon Style */
.profileIcon {
  background-color: rgba(255, 255, 255, 0) !important;

  /* border-radius: 50%; */
  /* object-fit: cover; */
}

/*  Profile  page Icon Style */
.profileContainer {
  background-color: #2f2d52;
  padding: 10px;
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  /* flex-direction: column !important; */
  gap: 20px;
}
.profileContainer img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
}

.userProfileHeading {
  color: lightgray;
  font-size: 24px;
}

.profileContainer form img {
  width: 32px;
  height: 32px;
}

.CurrentProfile {
  padding: 10px;
}

.userProfileHeading {
  text-align: center;
}

.tn {
  border-radius: 20% !important;
  width: 20px !important;
  height: 20px !important;
}

.btnContainer {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  gap: 10px;
}

.btnContainer button {
  background-color: #7b96ec;
  width: 100%;
  color: white;
  padding: 10px;
  font-weight: bold;
  border: none;
  cursor: pointer;
}

/* #deleteBtn {
  background-color: red;
} */

/* #backToChat {
  background-color: green;
} */

@media screen and (max-width: 768px) {
  .chat {
    width: 80%;
  }
  .sideBar {
    width: 20%;
  }
  .navBar {
    flex-wrap: wrap;
  }

  .user {
    padding-top: 10px;
    flex-wrap: wrap;
    flex-direction: column;
  }

  .userChat span {
    font-size: 12px;
  }

  .profileContainer {
    background-color: #2f2d52;

    padding: 10px;
    padding-top: 100px;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    gap: 20px;
    overflow: scroll;
  }
  .home .container {
    border: 1px solid white;
    border-radius: 10px;
    width: 100%;
    /* width: 100%; */
    height: 100%;
    /* height: 100%; */
    display: flex;
    overflow: hidden;
    border-radius: none;
  }

  .userChat img {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    object-fit: cover;
  }
}
